@use "../util/" as *;

html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  display: grid;
  place-items: center;
  padding: 0;
  margin: 0;
  background-color: var(--radial-grad-from);
}
