header {
  margin-bottom: 5.625rem;
  width: 100%;
  max-width: 44rem;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  border: 0.25rem solid var(--header-outline);
  border-radius: 0.3125rem;
  padding-block: 0.625rem;
  padding-inline: 1.25rem;
}
@media (min-width: 71.875em) {
  header {
    margin-bottom: 2.8125rem;
    border-radius: 0.9375rem;
    padding-block: 0.9375rem;
  }
}
header img {
  width: 3.125rem;
  height: 3.125rem;
}
@media (min-width: 71.875em) {
  header img {
    width: 6.875rem;
    height: 6.875rem;
  }
}
header h1 {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  border-radius: 0.3125rem;
  padding-block: 0.8125rem;
  padding-inline: 1.25rem;
  background-color: var(--white);
  color: var(--score-text);
  font-size: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 0.0625rem;
}
@media (min-width: 71.875em) {
  header h1 {
    padding-block: 1.25rem;
    padding-inline: 3rem;
    font-size: 0.8125rem;
  }
}
header h1 span {
  display: block;
  color: var(--dark-text);
  font-size: 1.75rem;
}
@media (min-width: 71.875em) {
  header h1 span {
    font-size: 2.8125rem;
  }
}

.step-1 {
  margin-bottom: 7.6875rem;
  width: 100%;
  max-width: 29.6875rem;
  background-image: url("../images/bg-pentagon.svg");
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-position: center center;
  background-size: 80% 80%;
}
@media (min-width: 71.875em) {
  .step-1 {
    margin-bottom: 0;
  }
}
.step-1 button {
  position: relative;
  width: 6.25rem;
  height: 6.25rem;
  border-radius: 50%;
  border-width: 0.6875rem;
  border-style: solid;
  padding: 0;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 2.125rem 2.375rem;
  cursor: pointer;
}
@media (min-width: 71.875em) {
  .step-1 button {
    width: 9.0625rem;
    height: 9.0625rem;
    border-width: 1.0625rem;
  }
}
.step-1 button::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  display: block;
  border-radius: inherit;
  width: 100%;
  height: 100%;
  border-top: 0.3125rem solid hsla(217deg, 16%, 45%, 0.5);
}
.step-1 button:focus {
  transform: translateY(5px);
  box-shadow: none;
}
.step-1 button:focus::before {
  border-top: none;
}
.step-1__blk1 {
  margin-bottom: 1.5rem;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: flex-end;
  height: 11.625rem;
}
@media (min-width: 71.875em) {
  .step-1__blk1 {
    margin-bottom: 2.3125rem;
    height: 17.4375rem;
  }
}
.step-1__blk1--scissors {
  align-self: flex-start;
  border-color: var(--scissors-grad-to);
  background-image: url("../images/icon-scissors.svg");
  box-shadow: 0 0.3125rem var(--scissors-grad-from);
}
.step-1__blk1--spock {
  border-color: var(--cyan-to);
  background-image: url("../images/icon-spock.svg");
  box-shadow: 0 0.3125rem var(--cyan-from);
}
.step-1__blk1--paper {
  border-color: var(--paper-grad-to);
  background-image: url("../images/icon-paper.svg");
  box-shadow: 0 0.3125rem var(--paper-grad-from);
}
.step-1__blk2 {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
}
.step-1__blk2--lizard {
  border-color: var(--lizard-grad-to);
  background-image: url("../images/icon-spock.svg");
  box-shadow: 0 0.3125rem var(--lizard-grad-from);
}
.step-1__blk2--rock {
  border-color: var(--rock-grad-to);
  background-image: url("../images/icon-paper.svg");
  box-shadow: 0 0.3125rem var(--rock-grad-from);
}

@media (min-width: 71.875em) {
  footer {
    justify-self: flex-end;
  }
}
footer button {
  border: 0.125rem solid var(--white);
  border-radius: 0.625rem;
  padding-block: 0.875rem;
  padding-inline: 2.375rem;
  background-color: transparent;
  color: var(--white);
  font-size: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.1875rem;
  cursor: pointer;
}

html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  display: grid;
  place-items: center;
  padding: 0;
  margin: 0;
  background-color: var(--radial-grad-from);
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --scissors-grad-from: hsl(39, 89%, 29%);
  --scissors-grad-to: hsl(40, 84%, 53%);
  --paper-grad-from: hsl(230, 89%, 42%);
  --paper-grad-to: hsl(230, 89%, 65%);
  --rock-grad-from: hsl(349, 71%, 32%);
  --rock-grad-to: hsl(349, 70%, 56%);
  --lizard-grad-from: hsl(261, 73%, 40%);
  --lizard-grad-to: hsl(261, 72%, 63%);
  --cyan-from: hsl(189, 59%, 33%);
  --cyan-to: hsl(189, 58%, 57%);
  --dark-text: hsl(229, 25%, 31%);
  --score-text: hsl(229, 64%, 46%);
  --header-outline: hsl(217, 16%, 45%);
  --radial-grad-from: hsl(214, 47%, 23%);
  --radial-grad-to: hsl(237, 49%, 15%);
}

:root {
  --font-body: "Barlow Semi Condensed", sans-serif;
}

.container {
  margin: 1.875rem;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.1;
  font-weight: 700;
}

p {
  margin: 0;
}

a:link,
a:visited,
a:hover {
  text-decoration: none;
}